@import "../../App.scss";
@import url('https://fonts.googleapis.com/css2?family=Roboto+Slab:wght@100..900&display=swap');

.menu-img{
    cursor: pointer;
    width: 40px;

    @include desktop {
       display: none;
    }
    @include tablet{
        display:  none;
    }

    @include mobile{
        display: block;
    }
}

.toggleContainer{
    position: absolute;
    width: 100%;
    height: 200px;
    background-color: #353536;
    left: 0;
    top: 100px;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    gap: 20px;
    z-index: 999;
    border-bottom: 2px solid white;
    border-radius: 20px;

    @include desktop{
        display: none;
    }

    @include tablet{
        display: none;
    }

    @include mobile{
        display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    }

    .img-container{
        display: flex;
        align-items: center;
        justify-content: center;
    }

    .img-menu{
        width: 30px;
        margin-right: 30px;
    }

    .a-toggleMenu{
        text-decoration: none;
        font-size: 30px;
        font-family: "Roboto Slab", serif;
        color: white;
        // font-weight: bolder;
        letter-spacing: 2px;
    }
}


